import React from 'react';
import logo from './logo1.png';
import './App.css';
import NavBar from "./navbar";

/*creating a fucntioninal component with a login details*/
class Header extends React.Component {

/*Setting up the fucntionlity of the login adn out out*/
  constructor(props){
      super(props);
      this.state ={loggedIn:false, userName:""}
  };

  LogIN = () => {
    this.setState({loggedIn:false})
  };

  LogOUt = () => {
    this.setState({loggedIn:true})
  };


  /*rendering the header with the loogin option, logo and company name*/
  render () {
    const Login = this.state.loggedIn;
    return(
      <div>
            {Login ? (
             <div className= "welcome">
                <h2>welcome Back ,{this.state.userName} </h2>
                <button onClick={this.LogIN}>Logout</button>
              </div>
            ) : (
              <div className= "welcome">
                <h2>Sign in to continue</h2>
                <input onChange={(e) => this.setState({userName : e.target.value.toUpperCase()})}/>
                <button onClick={this.LogOUt}>Sign in</button>
              </div>
            )}
            <br/><br/><br/><br/>

            <div className="App">
              <div className="bodyStructure">
                <div className="container">
                  <div className="row">
                    <div className="col-sm">
                      <header className="App-header">
                        <img src={logo} className="App-logo" alt="logo" />
                        <h1>
                          PJ Production <br/>
                        </h1>
                        <div>
                          the media Industry<br/>
                        </div><br/>
                      </header>
                    </div>
                  </div>
                </div>
              </div>
      <NavBar/>
    </div>

      </div>





      )
  }
}

export default Header;
