import React from "react";
import './App.css';
import { NavLink } from 'react-router-dom';

/*creating a fucntioninal component*/
function NavBar() {
  return (
    <div className="App">
      <div className="bodyStructure">
          <div className="container">
            <div className="row">
              <div className="col-sm">
                <div className="navigationBar ">
              {/*creating a navigation bar whihc target the active component to disapear*/}
                  <nav>
                      <ul>
                          <li className="nav1"><NavLink  to='/home' activeStyle ={{display: "none"}}>Home</NavLink></li> 
                          <li className="nav1 "><NavLink to='/services'activeStyle ={{display: "none"}}>Services</NavLink></li>
                          <li className="nav1"><NavLink to='/project'activeStyle ={{display: "none"}}>project</NavLink></li>
                          <li className="nav1"><NavLink to='/about' activeStyle ={{display: "none"}}>About</NavLink></li>
                          <li className="nav1"><NavLink to='/contact'activeStyle ={{display: "none"}}>Contact</NavLink></li> 
                        
                      </ul>
                  </nav>
                </div>
              </div>
            </div>
                   
          </div>
          
      </div>
    </div>

   )};
export default NavBar;
